.section-intro__text {
  padding: 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%;
  z-index: 1;
  font-family: "Sofia Sans Extra Condensed", sans-serif;
  text-shadow: none;
  font-size: 2.2rem;
  line-height: 135%;
  background: hsla(32, 89%, 96%, 0.686);
}
.section-intro__text P {
  margin-bottom: 1rem;
}
@media (min-width: 320.98px) and (max-width: 1920px) {
  .section-intro__text {
    font-size: calc(16px + 6 * (100vw - 320px) / 1600);
  }
}
@media screen and (max-width: 320px) {
  .section-intro__text {
    font-size: 16px;
  }
}
.section-intro__image {
  align-self: center;
  position: relative;
  display: inline-block;
  width: 100%;
  flex: 0 0 249px;
}
.section-intro__image img {
  position: relative !important;
  max-width: 100%;
  object-fit: cover;
}
.section-intro__image-title {
  position: absolute;
  bottom: 0;
  font-size: 18px;
  color: #fff;
  padding: 1rem 2rem;
}
.section-intro__news {
  width: 100%;
  height: 100%;
}

.news__body {
  text-shadow: none;
  height: 100%;
}
.news__frame {
  margin: 0 25px;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.news__content {
  flex: 1;
  background: rgba(254, 246, 237, 0.6862745098);
}
.news__title {
  background: rgba(254, 246, 237, 0.6862745098);
  padding-bottom: 1rem;
  position: relative;
  z-index: 3;
  border-bottom: 1px solid rgba(158, 158, 158, 0.3019607843);
  font-family: "Monomakh", sans-serif;
}
.news__wrapp-container {
  position: relative;
  padding: 1rem 0 0 0;
  z-index: 0;
  display: flex;
  height: 100%;
  flex-direction: row-reverse;
}
.news__info {
  position: relative;
  z-index: 1;
  overflow: hidden;
  display: grid;
  grid-template-columns: auto;
  justify-content: start;
  column-gap: 1rem;
  row-gap: 2rem;
  max-height: 52.8rem;
  z-index: 0;
  overflow-y: scroll;
}
.news__column {
  padding-right: 1rem;
}
.news__text {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  line-height: 135%;
}
.news__button {
  position: relative;
  z-index: 3;
  opacity: 0.5;
  padding: 0 0.5rem;
  margin-right: -2.2rem;
}
.news__image {
  margin: 0 1.6rem 0.5px 0;
  position: relative;
  height: auto;
  float: left;
}
.news__image img {
  max-width: 100%;
  width: 100%;
  max-width: 182px;
  object-fit: contain;
}

.menu-list {
  position: fixed;
  top: 0;
  right: 0;
  width: auto;
  height: 100vh;
  z-index: 13;
  transform: translateX(100%);
  transition: transform 0.5s cubic-bezier(0.7, -0.955, 0.55, 1.55);
}
@media screen and (max-width: 320px) {
  .menu-list {
    width: 100%;
  }
}
.menu-list._open-list {
  transition: transform 0.5s ease-in;
  transform: translateX(0);
}
.menu-list__body {
  position: relative;
  width: 100%;
  height: 100%;
}
.menu-list__content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.menu-list__header {
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: hsla(32, 89%, 96%, 0.686);
}
.menu-list__title {
  padding-bottom: 2rem;
  height: 8rem;
}
@media screen and (max-width: 490px) {
  .menu-list__title {
    padding-bottom: 1rem;
  }
}
.menu-list__title h2 {
  width: auto;
  font-size: 3.2rem;
  line-height: 1.1;
}
.menu-list__title h2 span {
  color: gray;
  line-height: 1;
}
.menu-list__title h2 span:nth-child(2) {
  white-space: nowrap;
}
.menu-list__list {
  position: relative;
  height: 100%;
  overflow-x: hidden;
  display: block;
  pointer-events: all;
  background-color: rgb(254, 246, 237);
  opacity: 0.8;
}
.menu-list__list ul {
  position: relative;
  z-index: 1;
}
.menu-list__list__frame-container,
.menu-list__list .frame-container {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.menu-list__list span {
  font-size: 2.2rem;
}
.menu-list__link {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr min-content;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
  column-gap: 2rem;
  transition: color 0.4s ease-in;
}
@media (min-width: 1024.99px) {
  .menu-list__link:hover {
    color: gray;
  }
}
.menu-list__link span {
  display: block;
  text-align: right;
  font-weight: 100;
  align-self: end;
}
.menu-list__link span:nth-child(1) {
  justify-self: start;
  white-space: nowrap;
}
@media screen and (max-width: 320px) {
  .menu-list__link span:nth-child(2) {
    justify-self: start;
    white-space: nowrap;
    grid-column: 1/2;
  }
  .menu-list__link span:nth-child(3) {
    justify-self: start;
    white-space: nowrap;
    grid-column: 2/3;
    grid-row: 3/-1;
    align-self: end;
  }
}
@media screen and (max-width: 320px) {
  .menu-list__link span {
    justify-self: start;
  }
}
@media screen and (max-width: 320px) {
  .menu-list__link {
    grid-template-columns: max-content min-content;
  }
}
.menu-list__list-item {
  padding: 0 1rem;
}
.menu-list__list-item:not(:last-child) {
  border-bottom: 1px solid rgba(30, 32, 46, 0.1411764706);
}

.arrow {
  position: relative;
  z-index: 9;
  margin-top: -40px;
  width: auto;
  height: 100px;
  top: 50%;
}
.arrow span {
  display: block;
  width: 15px;
  height: 15px;
  border-bottom: 5px solid var(--font-body-color);
  border-right: 5px solid var(--font-body-color);
  user-select: none;
  pointer-events: none;
  transform: rotate(45deg);
  animation: arrow 2s infinite;
}
.arrow span:nth-child(2) {
  animation-delay: -0.2s;
}
.arrow span:nth-child(3) {
  animation-delay: -0.4s;
}

@keyframes arrow {
  0% {
    opacity: 0;
    transform: translate(0, -20px) rotate(45deg);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translate(0, 20px) rotate(45deg);
  }
}